<template>
  <div id="calcres" class="round2">
    <div id="calcblocks">
      <div>
        <label for="tariff">Тариф</label>
        <select id="tariff" class="field" :value="tariff" @input="updateTariff($event.target.value)">
          <option value="avia" selected>Авиадоставка</option>
          <option value="auto">Автодоставка</option>
          <option value="train">Доставка поездом</option>
        </select>
        <ul>
          <li v-for="(el, index) in elValues" :key="el.id" :style="color(index, elValues.length)">
            <h4>Посылка #{{ index+1 }}</h4>
            <h2>{{ el.values.result }}</h2>
          </li>
        </ul>
      </div>
      <div>
        <p><input type="checkbox" name="insurance" value="insurance" v-model="isInsurance">Страховка</p>
        <label for="cost">Стоимость, USD</label>
        <input type="number" name="Cost" id="cost" class="field" :value="cost" @input="$emit('cost',format($event.target.value,0))" :disabled="!isInsurance">
        <h3>+ {{ insurance }}</h3>
        <p><input type="checkbox" name="fromMSK" value="fromMSK" v-model="isFromMSK">Доставка по СНГ</p>
        <h3>+ {{ fromMSK }}</h3>
        <p><input type="checkbox" name="legalpay" value="legalpay" v-model="isLegalPay">Оплата с юр. лица</p>
        <h3>+ {{ legalPay }}</h3>
      </div>
    </div>
    <h1>{{ totalResult }}</h1>
    <button class="btn" @click="$emit('attach',{isFromMSK: isFromMSK, isLegalPay: isLegalPay})">Прикрепить к заявке</button>
  </div>
</template>

<script>
import format from '../components/format.js'
import TariffsTable from '../assets/TariffsTable.json'
export default {
  name: 'CalcRes',
  props: ['tariff', 'elValues', 'cost'],
  data(){ return {
    isInsurance: true,
    isFromMSK: false,
    isLegalPay: false
  }},
  computed: {
    totalResult: function(){
      return  (+parseFloat(this.insurance)+parseFloat(this.fromMSK)+parseFloat(this.legalPay)+parseFloat(this.elValues.reduce((a, b) => parseFloat(a) + parseFloat(b.values.result), 0).toFixed(2))).toFixed(2)
    },
    insurance: function(){
      if(this.isInsurance){
        return parseFloat((this.cost*TariffsTable.insrate).toFixed(2))
      } else {
        this.$emit('cost','')
        return 0
      }
    },
    fromMSK: function(){
      if(this.isFromMSK){
        return TariffsTable.fromMSK
      } else {
        return 0
      }
    },
    legalPay: function(){
      if(this.isLegalPay){
        return TariffsTable.legalPay
      } else {
        return 0
      }
    }
  },
  mixins: [format],
  methods: {
    updateTariff: function (value) {
      this.$emit('updateTariff', value);
    },
    color: function (index, length) {
      let h = (410-360*Math.pow(index/length,3))%360
      let s = (100-60*Math.pow(index/length,0.5))
      let l = 30
      return {
        color: 'hsl( '+ h +', '+ s +'%, '+ l +'%)'
      }
    }
  }
}
</script>

<style scoped>
#calcres {
  flex-shrink: 0;
  width: 200px;
  background-color: #f5f5f5;
  padding: 10px;
  margin: 10px;
}

#calcblocks {
  display: flex;
  flex-direction: column;
}

#calcblocks>* {
  flex: auto; 
}

label {
  text-align: left;
}
.field {
  margin: auto;
  width: 150px;
}
ul {
  list-style-type: none;
  padding: 0;
}
p {
  margin: 0 0 0 40px;
  text-align: left;
}
p>* { display: inline-block; }

h4 {
  text-align: left;
  margin: 0 10px;
}
h3 {
  text-align: right;
  margin: 0 10px;
}
h2 {
  text-align: left;
  margin: 0 20px;
}
h1 {
  text-align: right;
  margin: 0 20px;
  border-top-style: solid;
  border-width: 1px;
}
@media screen and (max-width: 1000px) {
  #calcres {
    width: 400px;
  }
  #calcblocks {
    flex-direction: row;
  }
  #calcblocks>*:last-child {
  align-self: flex-end;
  }   
}
@media screen and (max-width: 600px) {
  #calcres {
    width: 260px;
  }
  #calcblocks {
    flex-direction: column;
  }
  #calcblocks>*:last-child {
  align-self: auto;
  }  
  p {
    margin: 0 0 0 80px;
  }
}
</style>