<template>
  <div id="calcel" class="round2">
    <div v-if="!last" id="calcel2">
      <h3>
        Посылка #{{id+1}}
        <button class="bluebtn" @click="eventRemove">×</button>
      </h3>

      <CalcIn :tariff="tariff" :values="values" @values="updateVal"/>
    </div>
    <div v-if="last" id="add">
      <button class="btn" @click="eventAdd">╋</button>
    </div>
  </div>
</template>

<script>
import CalcIn from '../components/CalcIn.vue'
export default {
  name: 'CalcEl',
  props: ['values', 'id', 'tariff', 'last'],
  components: {
    CalcIn
  },
  methods: {
    eventRemove: function(){
      this.$emit('remove');
    },
    eventAdd: function(){
      this.$emit('add');
    },
    updateVal: function(values){
      this.$emit('values', values);
    }
  }
}
</script>

<style scoped>

#calcel {
  margin: 10px;
}
#calcel2 {
  width: 260px;
  padding: 10px;
  
}

.bluebtn {
  font-size: 20px;
  font-family: monospace;

  background-color: rgba(255,255,255,0.5);
  color: #17495C;
  border-style: none;
  border-radius: 2px;
  transition: 0.2s ease-out;
}
.bluebtn:hover{background-color: rgba(255,255,255,0.8)}
.bluebtn:active{background-color: rgba(0,0,0,0.1)}
.bluebtn:focus {
  outline: none;
}
#add {
  width: 0;
  height: 245px;
  display: flex;
  align-items: center;
}
#add button {
  flex-shrink: 0;
  border-radius: 100% ;
  width: 40px;
  height: 40px;
}

h3 {
  text-align: end;
  margin: 0 0 10px 0;
}

@media screen and (max-width: 600px) {
  #add {
    width: auto;
    height: auto;
  }
}
</style>