<template>
  <Layout>
    <Calculator id="calculator" :tariff="tariff" @updateTariff="tariff = $event" @attachData="attachData"/>
    <TariffsContent id="tariffs" @updateTariff="tariff = $event"/>
    <Application id="application" ref="application"/>
  </Layout>
</template>


<script>
import Layout from '../Layout.vue'

import Calculator from '../components/Calculator.vue'
import Application from '../components/Application.vue'
import TariffsContent from '../components/TariffsContent.vue'

export default {
  name: 'App',
  components: {
    Layout,
    Calculator,
    TariffsContent,
    Application
  },
  data(){ return{
    tariff: 'avia'
  }},
  methods: {
    attachData: function(Data) {
      this.$refs.application.attachData(Data)
    }
  }
}
</script>

<style>
#calculator{grid-area: calc}
#tariffs{grid-area: tariffs}
#application{grid-area: application}

#content {
  display: grid;
  grid-template-areas:
    ". calc calc calc ."
    ". . tariffs . ."
    ". . application . .";
  grid-template-columns: 1fr 2fr 900px 2fr 1fr;
  grid-template-rows: auto auto auto;

  grid-row-gap: 20px;
}

@media screen and (max-width: 1000px) {
  #content {
    grid-template-columns: 0 0 auto 0 0;
  }
}
</style>