<template>
<table>
	<thead>
	<tr>
		<th></th>
		<th>Срок доставки</th>
        <th>Минимальная Стоимость</th>
	</tr>
	</thead>
	<tbody>
	<tr>
		<th>Самолет</th>
		<td>3-4 дня</td>
		<td>10 USD</td>
    <button class="btn" value="avia" @click="updateTariff($event.target.value)">Выбрать</button>
	</tr>
	<tr>
		<th>Автодоставка</th>
		<td>1-2 недели</td>
		<td>10 USD</td>
    <button class="btn" value="auto" @click="updateTariff($event.target.value)">Выбрать</button>
	</tr>
	<tr>
		<th>Поезд</th>
		<td>2 недели</td>
		<td>20 USD</td>
    <button class="btn" value="train" @click="updateTariff($event.target.value)">Выбрать</button>
	</tr>
	</tbody>
</table>
</template>

<script>
export default {
  name: 'Tariffs',
  methods: {
    updateTariff: function (value) {
      this.$emit('updateTariff', value);
    }
  }
}
</script>

<style>
/* Force table to not be like tables anymore */
table, thead, th, td, tr { 
	display: block; 
}
tbody {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
	/* Hide table headers (but not display: none;, for accessibility) */
thead tr { 
	position: absolute;
	top: -9999px;
	left: -9999px;
}
th {
  width: 160px;
  
}
tr {
  background: linear-gradient(150deg, #afdfd0, #c5f3b9);
  border-radius: 6px;
  padding: 10px;
  margin: 5px;
}
	
td { 
	/* Behave  like a "row" */
	border: none;
	border-top: 2px solid #e5facd; 
	position: relative;
	padding-top: 20px; 
}
	
td:before { 
	/* Now like a table header */
	position: absolute;
    text-align: center;
	/* Top/left values mimic padding */
	top: 6px;
	left: 6px;
	width: 160px;
	padding-right: 10px;

	font-size: 0.75rem;
}
	
/*Label the data*/
td:nth-of-type(1):before { content: "Срок доставки"; }
td:nth-of-type(2):before { content: "Минимальная стоимость"; }

@media screen and (max-width: 600px) {
  table {
    overflow: scroll;
    width: 100vw;
  }
  tbody {
	justify-content: start;
    flex-wrap: nowrap;
  }
  tr {
	margin: 10px;
  }
}
</style>